/* базовая стилизация для страницы lips */
body {
    font-family: 'Arial', sans-serif;
    font-weight:400;
	margin: 0;
	padding: 0;
	font-size: 13px;
    line-height: 17px;
    
}

h1 {
	font-family: 'Arial', sans-serif;
    font-size: 48px;
    font-weight:700;
	line-height: 36px;
	margin-left: 20px;
}

h2 {

    font-family: 'Arial', sans-serif;
    margin-bottom: 10px;
    font-size: 24px;
    font-weight:700;
	line-height: 36px;
	margin-left: 65px;
}

main {
    width: 835px;
    margin: 110px auto 0 150px;
}

table {
    width: 100%;
    border-collapse: collapse;
    float: right;
    height: 110px;
}
table, th, td {
    font-family: 'Arial', sans-serif;
    height: 28px;
    font-weight:400;
    text-align: center;
    border: 1px solid black;
  }

td  {
    width: 315px;
    text-align: center;
    height: 30px;
}

th {
    height: 33px;
}

a, 
a:hover {
    color: rgb(210, 136, 164);
    font-family: 'Arial', sans-serif;
    margin-bottom: 0;
    font-size: 11px;
    font-weight:400;
	line-height: 12px;
	margin-left: 0px;
}

.header-text-wrapper {
    overflow: auto; 
}



.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	clip: rect(0 0 0 0);
	overflow: hidden;
}

.header-text {
    margin-top: 10px;
    margin-left: 8px;
    margin-bottom: 10px;
    font-size: 32px;
    text-align: left;


    

}

.contact-form {
    display: flex;
    justify-content: space-around;
    margin-left: 25px;
    align-items: stretch;
    background-color: rgb(255, 244, 244);
    width: 646px;
    height: 91px;
    margin-bottom: 10px;

}


.contact-form__right {
    display: flex;
    flex-direction: column;
    align-content: flex-start;

}

.contact-form__left {
    display: flex;
    flex-direction: column;


}

button,
    .button {
		font-family: 'Arial', sans-serif;
        padding: 4px 35px;
        border-radius: 4px;
        border: 1px solid #eb9d9d;
        background: linear-gradient(rgb(249, 126, 126), rgb(238, 93, 93));
        color: #fcfcfc;
        content: "Кнопка";
    }

.services {
    display: flex;
    justify-content: center;
    
}

.services__left {
    margin-top: 9px;
    margin-left: -35px;
    margin-right: 68px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 181px;
    height: 149px;
    
}

.services__right {
    margin-top: 13px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 181px;
    height: 149px;
    
}

.services__right__link {
    margin: 0px;
}
.text-story-columns {
    
    margin-right: 15px;
    display: flex;
    justify-content: space-between;
}
.text-story-columns__text {
    margin-top: 6px;
    margin-left: 44px;

    width: 320px;
    height: 300px;
}

/* 63px -разница  глаза и текст распределить на margin*/

.text-story-article {
    display: flex;
    margin-left: 40px;
    height: 90px;
}

.text-story {
    display: flex;
    justify-content: flex-start;
    margin-left: 50px;

}

.text-story__text {
    margin-left: 10px;
    margin-top: 60px;
    margin-bottom: auto;

    width: 290px;
    height: 250px;
}

.text-story-center {
    padding-top: 0px;
    margin-left: 45px;
    height: 320px;
}

.text-story-center__text {
    
    margin-top: 20px;
}

.text {
    margin-right: 0px;

}

.advantages {
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    padding: 0;
}

.advantages-header {
    padding: 0;
    margin: 5px;
    text-align: center;
}

.advantages__list-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.advantages__list-item-text {
    width: 97px;
    text-align: center;
    font-weight: 700;
}

.specialist {
    display: flex;
    width: 543px;

}

.specialist__text {
    margin-left: 5px;
}

.specialist__text-list-item {
    font-family: 'Arial', sans-serif;
    font-weight:400;
    margin-top: 0px;
    margin-bottom: 0px;
	padding: 0;
	font-size: 13px;
    line-height: 11px;

}

.block-paragraph {
    display: block;
    margin: 0;
    padding: 0px;
    
}

/*для слайдера */
.swiper-container {
    width: 100%;
    height: 128px;
  }

  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;

    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }


  .swiper-button-next,
  .swiper-button-prev {
    width: 74px;
    height: 31px;

  }
